import React from "react";
import { Button } from "@material-ui/core";
import { Link } from "react-router-dom";
import { getCurrentUser } from "../services/authService";

const Home = () => {
  const user = getCurrentUser();
  return (
    <div className="navbar flex">
      <div className="logo">domain</div>
      <div className="btns flex">
        {user && (
          <React.Fragment>
            <div style={{ margin: "0 5px", fontWeight: "bold" }}>
              {user.name}
            </div>
            <Link to="/api/auth/logout" className="remove-underline">
              <Button className="btn" variant="outlined" type="submit">
                Logout
              </Button>
            </Link>
          </React.Fragment>
        )}
        {!user && (
          <React.Fragment>
            <Link to="/api/auth/login" className="remove-underline">
              <Button className="btn" variant="outlined" type="submit">
                login
              </Button>
            </Link>
            <Link to="/api/auth/register" className="remove-underline">
              <Button className="btn" variant="outlined" type="submit">
                register
              </Button>
            </Link>
          </React.Fragment>
        )}
      </div>
    </div>
  );
};

export default Home;
