import { resetEmail } from "../services/resetService";
import Joi from "joi";
import Form from "./common/form";
import EmailOutlinedIcon from "@material-ui/icons/EmailOutlined";
import "../styles/form.css";

class ResetEmail extends Form {
  state = { data: { email: "" }, errors: {} };

  schema = {
    email: Joi.string()
      .min(3)
      .max(250)
      .required()
      .email({ tlds: { allow: false } }),
  };

  doSubmit = async () => {
    try {
      const { data } = this.state;
      await resetEmail({ email: data.email });
      this.props.history.push(
        `/api/verify/password-reset/resend-email/${data.email}`
      );
    } catch (error) {
      if (error.response && error.response.status === 400) {
        const errors = { ...this.state.errors };
        errors.email = error.response.data;
        this.setState({ errors });
      }
    }
  };

  render() {
    return (
      <div className="flex flex-column">
        <h1 className="heading">forgot password?</h1>
        <form
          onSubmit={this.handleSubmit}
          className="form-container flex flex-column"
        >
          {this.renderInput(
            "email",
            "Email",
            <EmailOutlinedIcon />,
            "email",
            true
          )}
          {this.renderSubmitBtn("Submit")}
        </form>
      </div>
    );
  }
}

export default ResetEmail;
