import http from "./httpService";

const baseUrl = process.env.REACT_APP_API_URL;

const emailVerifyResendEndPoint = baseUrl + "/verify/send-email/account";

export function emailVerifyResend(email) {
  return http.post(emailVerifyResendEndPoint, email);
}

export function emailVerify(userId, token) {
  return http.get(`${baseUrl}/verify/email/${userId}/${token}`);
}

export function emailLinkVerify(userId, token) {
  return http.get(`${baseUrl}/verify/email-link/${userId}/${token}`);
}
