import React from "react";
import Joi from "joi";
import Form from "./common/form";
import { Link } from "react-router-dom";
import { login } from "../services/authService";
import EmailOutlinedIcon from "@material-ui/icons/EmailOutlined";
import LockOutlinedIcon from "@material-ui/icons/LockOutlined";
import "../styles/form.css";

class Login extends Form {
  state = { data: { email: "", password: "" }, errors: {} };

  schema = {
    email: Joi.string()
      .min(3)
      .max(250)
      .required()
      .email({ tlds: { allow: false } }),
    password: Joi.string().min(3).max(1000).required(),
  };

  doSubmit = async () => {
    try {
      const { data } = this.state;
      const { data: jwt } = await login(data.email, data.password);

      if (jwt.message === "verify_email") {
        return this.props.history.push(
          `/api/verify/account/resend-email/${jwt.email}`
        );
      }

      localStorage.setItem("token", jwt);
      this.props.history.push("/");
    } catch (error) {
      if (error.response && error.response.status === 400) {
        const errors = { ...this.state.errors };
        errors.email = error.response.data;
        errors.password = error.response.data;
        this.setState({ errors });
      }
    }
  };

  render() {
    return (
      <div className="flex flex-column">
        <h1 className="heading">Login</h1>
        <form
          onSubmit={this.handleSubmit}
          className="form-container flex flex-column"
        >
          {this.renderInput(
            "email",
            "Email",
            <EmailOutlinedIcon />,
            "email",
            true
          )}
          {this.renderInput(
            "password",
            "Password",
            <LockOutlinedIcon />,
            "password",
            true
          )}
          <Link to="/api/verify/send-email/password-reset">
            <p
              style={{
                color: "var(--blue)",
                fontSize: "15px",
                margin: "5px 0 5px 120px",
              }}
            >
              Forgot password?
            </p>
          </Link>
          {this.renderSubmitBtn("Login")}
        </form>
      </div>
    );
  }
}

export default Login;
