import React from "react";
import { Route, Switch, Redirect } from "react-router-dom";
import { ToastContainer } from "react-toastify";
import Home from "./components/home";
import Register from "./components/register";
import NotFound from "./components/notFound";
import Login from "./components/login";
import EmailVerifyResend from "./components/emailVerifyResend";
import ResetEmailResend from "./components/resetEmailResend";
import EmailVerify from "./components/emailVerify";
import ResetEmail from "./components/resetEmail";
import ResetVerify from "./components/resetVerify";
import Logout from "./components/logout";
import "react-toastify/dist/ReactToastify.css";
import "./App.css";

const App = () => {
  return (
    <React.Fragment>
      <ToastContainer />
      <Switch>
        <Route exact path="/" component={Home}></Route>
        <Route exact path="/api/auth/register" component={Register}></Route>
        <Route exact path="/api/auth/login" component={Login}></Route>
        <Route exact path="/api/auth/logout" component={Logout}></Route>
        <Route
          exact
          path="/api/verify/send-email/password-reset"
          component={ResetEmail}
        ></Route>
        <Route
          exact
          path="/api/verify/account/email/:userId/:token"
          component={EmailVerify}
        ></Route>
        <Route
          exact
          path="/api/verify/account/resend-email/:email"
          component={EmailVerifyResend}
        ></Route>
        <Route
          exact
          path="/api/verify/password-reset/resend-email/:email"
          component={ResetEmailResend}
        ></Route>
        <Route
          exact
          path="/api/verify/password-reset/:userId/:token"
          component={ResetVerify}
        ></Route>
        <Route path="/not-found" component={NotFound}></Route>
        <Redirect to="/not-found"></Redirect>
      </Switch>
    </React.Fragment>
  );
};

export default App;
