import Grid from "@material-ui/core/Grid";
import { TextField } from "@material-ui/core";

const Input = ({ error, icon, ...rest }) => {
  return (
    <Grid
      className="input-spacing"
      container
      spacing={1}
      justify="center"
      alignItems={error ? "center" : "flex-end"}
    >
      <Grid style={{ color: "var(--blue)" }} item>
        {icon}
      </Grid>
      <Grid item>
        {error ? (
          <TextField
            error
            helperText={error}
            style={{ width: "200px" }}
            {...rest}
          />
        ) : (
          <TextField style={{ width: "200px" }} {...rest} />
        )}
      </Grid>
    </Grid>
  );
};

export default Input;
