import React, { Component } from "react";
import { toast } from "react-toastify";
import { Button } from "@material-ui/core";
import { emailVerify } from "../services/emailService";
import checked from "../assets/checked.svg";
import warning from "../assets/warning.svg";
import "../styles/form.css";

class EmailVerify extends Component {
  state = {
    sucess: false,
    errors: false,
    userId: this.props.match.params.userId,
    token: this.props.match.params.token,
  };

  async componentDidMount() {
    const { userId, token } = this.state;
    try {
      await emailVerify(userId, token);
      this.setState({ sucess: true });
    } catch (error) {
      toast.error(error.response.data);
      this.setState({ errors: true });
    }
  }

  render() {
    const { errors, sucess } = this.state;

    return (
      <div style={{ marginTop: "50px" }} className="flex flex-column">
        <div
          className="form-container flex flex-column"
          style={{ width: "400px", height: "300px" }}
        >
          {sucess && (
            <React.Fragment>
              <img
                alt="checked"
                style={{ width: "25%", height: "25%" }}
                src={checked}
              />
              <h2 className="text-center">Thank You</h2>
              <h5 className="text-center" style={{ margin: "0" }}>
                You have verified your email.
              </h5>
              <Button
                onClick={() => (window.location = "/")}
                style={{
                  color: "var(--blue)",
                  borderColor: "var(--blue)",
                  width: "200px",
                  margin: "30px 0",
                }}
                variant="outlined"
              >
                continue
              </Button>
            </React.Fragment>
          )}
          {errors && (
            <React.Fragment>
              <img
                alt="warning"
                style={{ width: "25%", height: "25%" }}
                src={warning}
              />
              <h5 className="text-center">Link expired or invalid link</h5>
            </React.Fragment>
          )}
        </div>
      </div>
    );
  }
}

export default EmailVerify;
