import React from "react";
import { Button } from "@material-ui/core";
import checked from "../../assets/checked.svg";
import warning from "../../assets/warning.svg";
import "../../styles/form.css";

const EmailResend = ({ email, resendLink, errors }) => {
  return (
    <div style={{ marginTop: "50px" }} className="flex flex-column">
      <div
        className="form-container flex flex-column"
        style={{ width: "400px", height: "300px" }}
      >
        {!errors && (
          <React.Fragment>
            <img
              alt="checked"
              style={{ width: "25%", height: "25%" }}
              src={checked}
            />
            <h5 className="text-center">
              A link has been sent to your email account
            </h5>
            <h6 className="text-center" style={{ margin: "0" }}>
              Please click on the link that has just been sent to your email
              account : {email} .
            </h6>
            <Button
              onClick={resendLink}
              style={{
                color: "var(--blue)",
                borderColor: "var(--blue)",
                width: "200px",
                margin: "30px 0",
              }}
              variant="outlined"
            >
              resend link
            </Button>
          </React.Fragment>
        )}
        {errors && (
          <React.Fragment>
            <img
              alt="warning"
              style={{ width: "25%", height: "25%" }}
              src={warning}
            />
            <h5 className="text-center">Link expired or invalid link</h5>
          </React.Fragment>
        )}
      </div>
    </div>
  );
};

export default EmailResend;
