import EmailResend from "./common/emailResend";
import { emailVerifyResend } from "../services/emailService";
import { toast } from "react-toastify";
import { useState } from "react";

const EmailVerifyResend = (props) => {
  const [errors, setErrors] = useState(false);
  const email = props.match.params.email;
  const data = { email: email };

  const resendLink = async () => {
    try {
      const result = await emailVerifyResend(data);
      toast.success(result.data);
    } catch (error) {
      if (error.response && error.response.status === 400) {
        setErrors(true);
        toast.error(error.response.data);
      }
    }
  };

  return <EmailResend errors={errors} email={email} resendLink={resendLink} />;
};

export default EmailVerifyResend;
