import http from "./httpService";

const baseUrl = process.env.REACT_APP_API_URL;

const resetEmailEndpoint = baseUrl + "/verify/send-email/password-reset";

export function resetEmail(email) {
  return http.post(resetEmailEndpoint, email);
}

export function passwordReset(userId, token, password) {
  return http.post(
    `${baseUrl}/verify/password-reset/${userId}/${token}`,
    password
  );
}
