import React from "react";
import { emailLinkVerify } from "../services/emailService";
import { passwordReset } from "../services/resetService";
import { toast } from "react-toastify";
import Joi from "joi";
import Form from "./common/form";
import LockOutlinedIcon from "@material-ui/icons/LockOutlined";
import warning from "../assets/warning.svg";
import "../styles/form.css";

class ResetVerify extends Form {
  state = {
    sucess: false,
    error: false,
    userId: this.props.match.params.userId,
    token: this.props.match.params.token,
    data: {
      password: "",
      confirmPassword: "",
    },
    errors: {},
  };

  schema = {
    password: Joi.string().min(3).max(1000).required(),
    confirmPassword: Joi.string().min(3).max(1000).required(),
  };

  async componentDidMount() {
    const { userId, token } = this.state;
    try {
      await emailLinkVerify(userId, token);
      this.setState({ sucess: true });
    } catch (error) {
      toast.error("Link expired or invalid link");
      this.setState({ error: true });
    }
  }

  doSubmit = async () => {
    const { data, userId, token } = this.state;
    if (data.password !== data.confirmPassword) {
      const errors = { ...this.state.errors };
      errors.password = "password does not match";
      errors.confirmPassword = "password does not match";
      this.setState({ errors });
      return;
    }
    try {
      await passwordReset(userId, token, { password: data.password });
      window.location = "/";
    } catch (error) {
      if (error.response && error.response.status === 400) {
        this.setState({ error: true });
        toast.error(error.response.data);
      }
    }
  };

  render() {
    const { sucess, error } = this.state;
    return (
      <div className="flex flex-column">
        {sucess && (
          <React.Fragment>
            {" "}
            <h1 className="heading">reset password</h1>
            <form
              onSubmit={this.handleSubmit}
              className="form-container flex flex-column"
            >
              {this.renderInput(
                "password",
                "Password",
                <LockOutlinedIcon />,
                "password",
                true
              )}
              {this.renderInput(
                "confirmPassword",
                "Confirm password",
                <LockOutlinedIcon />,
                "password",
                true
              )}
              {this.renderSubmitBtn("Submit")}
            </form>
          </React.Fragment>
        )}
        {error && (
          <React.Fragment>
            <div
              className="form-container flex flex-column"
              style={{ marginTop: "50px", width: "400px", height: "300px" }}
            >
              <img
                alt="warning"
                style={{ width: "25%", height: "25%" }}
                src={warning}
              />
              <h5 className="text-center">Link expired or invalid link</h5>
            </div>
          </React.Fragment>
        )}
      </div>
    );
  }
}

export default ResetVerify;
